"use client";

import { Checkbox, Input, InputNumber } from "antd";
import { useState } from "react";
import BudgetSlider from "../components/BudgetSlider/BudgetSlider";
import TagInput from "../components/TagInput/TagInput";

export default function Form() {
    const [autoResponses, setAutoResponses] = useState(false);

    return (
        <form className="max-w-md mx-auto p-4 bg-white shadow rounded-lg">
            <h2 className="text-lg text-cyan-700 font-semibold mb-4">Создать задачу</h2>

            {/* Название задачи */}
            <label htmlFor="taskTitle" className="block text-sm text-cyan-700 mb-1">Название задачи</label>
            <Input id="taskTitle" className="w-full mb-3" placeholder="Введите название" />

            {/* Описание задачи */}
            <label htmlFor="taskDesc" className="block text-sm text-cyan-700 mb-1">Описание</label>
            <Input id="taskDesc" className="w-full mb-3" placeholder="Введите описание" />

            {/* Добавление тегов */}
            <TagInput />

            {/* Слайдер бюджета */}
            <BudgetSlider />

            {/* Срок в днях */}
            <label htmlFor="taskDeadline" className="block text-sm text-cyan-700 mb-1">Срок в днях</label>
            <Input id="taskDeadline" type="number" defaultValue={1} className="w-full mb-3" />

            {/* Количество напоминаний */}
            <label htmlFor="reminders" className="block text-sm text-cyan-700 mb-1">Количество напоминаний</label>
            <InputNumber id="reminders" defaultValue={1} className="w-full mb-3" />

            {/* Чекбокс автоматических ответов */}
            <div className="flex items-center space-x-2 pt-2">
                <Checkbox id="allAutoResponses" checked={autoResponses} onChange={(e) => setAutoResponses(e.target.checked)} />
                <label htmlFor="allAutoResponses" className="text-sm text-gray-700">Включить автоматические ответы</label>
            </div>

            {/* Кнопка отправки */}
            <button className="mt-4 w-full bg-blue-500 text-white py-2 rounded hover:bg-blue-600 transition">
                Отправить
            </button>
        </form>
    );
}
